.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  background-color: #ececec;
}

/* Header */
/* ==============================*/

.logo__img {
  display: inline-block;
  height: 35px;
  width: 55px;

  margin-right: 20px;
}

.nav__button {
  border-bottom: 1px solid transparent;
}

.nav__button:hover {
  border-bottom: 1px solid #333;
}

.lang__button {
  margin-right: 10px;
}

/* Slider */
/* ==============================*/

.carousel-inner {
  background-color: #282c34;
}

.mask {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background: #333;
}

.slider__mask {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
}

.slider__img {
  height: 100vh;
}

.carousel-item {
  transition: transform .6s ease-out;
  height: 100%;
}

.carousel-item-next, .carousel-item-left {
  float: right;
  transform: translateX(-100%);
  transition: transform .01s ease-out;
}

.carousel-item-prev, .carousel-item-right {
  float: left;
  transform: translateX(100%);
  transition: transform .001s ease-out;
}

.sr-only {
  display: none;
}

.carousel-indicators {
  height: 20px;
}

.carousel-indicators li {
  width: 40px;
  height: 4px;
  margin: 5px;

  background-color:rgba(255, 255, 255, 0.4);

  cursor: pointer;
}

.carousel-indicators li.active {
  background-color:rgba(255, 255, 255, 0.9);
}

/* Best */
/* ==============================*/

.best__card {
  background: linear-gradient(180deg, rgba(rgb(3, 3, 3), .15), rgba(white, 0));
}

/* Developers */
/* ==============================*/
.developers__card {
  color: #fff;
  position: relative;

  cursor: pointer;
}

.developers__photo{
  transition: opacity .2s linear;
}


.developers__card:hover .developers__photo {
  opacity: .5;
}

.developers__icon {
  position: absolute;
  top: 4%;
  left: 50%;
  z-index: 1;

  padding: 60px;

  transform: translate(-50%);

  font-size: 120px;
  color: rgb(216, 216, 216);

  opacity: 0;
  transition: opacity .2s linear;
}

.developers__icon--light {
  color: rgb(216, 216, 216);
}

.developers__card:hover .developers__icon {
  opacity: 1;
}

.developers__link {
  color: inherit;
  text-decoration: none;
}

.developers__link:hover {
  color: inherit;
}



/* Footer */
/* ==============================*/

.footer__logo {
  color: inherit;
  text-decoration: none;
}

.footer__logo:hover {
  color: inherit;
}

.social__icon {
  font-size: 20px;
  margin: 4px 4px;

  cursor: pointer;

  transition: color .2s linear, transform .2s linear;
}

.social__icon:hover {
  transform: scale(1.2);
  color: #f3f3f3;
}

.social__icons a:first-of-type {
  margin-left: 20px;
}

